import React, { Component } from 'react';
import ReactDataSheet from 'react-datasheet';
// Be sure to include styles at some point, probably during your bootstrapping
import 'react-datasheet/lib/react-datasheet.css';
import { Col, Row, Button, Modal, message } from 'antd';
import { planType } from 'dump/data';
import './styles.scss';
import FormSelectPlan from './FormSelectPlan';

export default class DataSheet extends Component {
  state = {
    visibleSelectPlan: false,
    grid: [
      [
        { value: 'Q', code: 1 },
        { value: '', code: 2 },
        { value: '', code: 3 },
        { value: '', code: 4 },
        { value: '', code: 5 },
        { value: '', code: 6 },
        { value: '', code: 7 },
        { value: '', code: 8 },
      ],
      [
        { value: '', code: 9 },
        { value: '', code: 10 },
        { value: '', code: 11 },
        { value: '', code: 12 },
        { value: '', code: 13 },
        { value: '', code: 14 },
        { value: '', code: 15 },
        { value: '', code: 16 },
      ],
    ],
    codeSelected: [],
  };

  showModalSelectPlan = () => {
    if (document.getElementsByClassName('selected').length === 0) {
      message.warning('Vui lòng chọn thời gian');
      return;
    }
    const codeSelected = {};
    Array.from(document.getElementsByClassName('selected')).forEach(item => {
      codeSelected[item.dataset.code] = true;
    });
    console.log(codeSelected);
    this.setState({
      visibleSelectPlan: true,
      codeSelected,
    });
  };
  handleOkSelectPlan = () => {
    this.setState({
      visibleSelectPlan: false,
    });
  };

  handleSelectPlan = planCode => {
    const { grid, codeSelected } = this.state;
    const gridObjCodeSelect = {};
    grid.forEach(item => {
      item.forEach(item2 => {
        if (codeSelected[item2.code]) {
          item2.value = planCode;
          gridObjCodeSelect[item2.code] = item2;
        }
      });
    });
    const gridNew = JSON.parse(JSON.stringify(grid));
    this.setState({ grid: gridNew });
    this.handleOkSelectPlan();
  };

  handleCancelSelectPlan = e => {
    console.log(e);
    this.setState({
      visibleSelectPlan: false,
    });
  };

  handleDeletePlan = () => {
    if (document.getElementsByClassName('selected').length === 0) {
      message.warning('Vui lòng chọn thời gian');
      return;
    }
    const codeSelected = {};
    Array.from(document.getElementsByClassName('selected')).forEach(item => {
      codeSelected[item.dataset.code] = true;
    });
    const { grid } = this.state;
    const gridObjCodeSelect = {};
    grid.forEach(item => {
      item.forEach(item2 => {
        if (codeSelected[item2.code]) {
          item2.value = '';
          gridObjCodeSelect[item2.code] = item2;
        }
      });
    });
    const gridNew = JSON.parse(JSON.stringify(grid));
    this.setState({ grid: gridNew });
  };
  render() {
    return (
      <Row>
        <Col span={24}>
          <ReactDataSheet
            style={{ width: '100%' }}
            data={this.state.grid}
            onCellsChanged={changes => {
              console.log(changes);
              const grid = this.state.grid.map(row => [...row]);
              changes.forEach(({ row, col, value }) => {
                grid[row][col] = { ...grid[row][col], value };
              });
              this.setState({ grid });
            }}
            sheetRenderer={props => (
              <table
                style={{ width: '100%' }}
                className={`${props.className} my-awesome-extra-class`}
              >
                <thead>
                  <tr>
                    <th className="header-action" colSpan="10">
                      <Button onClick={this.handleDeletePlan}>Xóa kế hoạch</Button>
                      <Button onClick={this.showModalSelectPlan}>Xếp kế hoạch</Button>
                    </th>
                  </tr>
                  <tr>
                    <th rowSpan="3">Tên lớp</th>
                    <th rowSpan="3">Số SV</th>
                    <th colSpan="4">Th 8/2019</th>
                    <th colSpan="4">Th 9/2019</th>
                  </tr>
                  <tr>
                    <th>1</th>
                    <th>2</th>
                    <th>3</th>
                    <th>4</th>
                    <th>5</th>
                    <th>6</th>
                    <th>7</th>
                    <th>8</th>
                  </tr>
                  <tr>
                    <th>09-12</th>
                    <th>13-19</th>
                    <th>20-26</th>
                    <th>27-02</th>
                    <th>03-09</th>
                    <th>10-16</th>
                    <th>17-23</th>
                    <th>24-30</th>
                  </tr>
                </thead>
                <tbody>{props.children}</tbody>
              </table>
            )}
            rowRenderer={props => (
              <tr>
                <td>SCOT</td>
                <td>100</td>
                {props.children}
              </tr>
            )}
            valueRenderer={cell => (
              <div
                style={{
                  background: !planType[cell.value] ? '#fff' : planType[cell.value].color,
                  height: 27,
                  textAlign: 'center',
                  verticalAlign: 'middle',
                  lineHeight: '27px',
                  color: '#fff',
                }}
              >
                {cell.value}
              </div>
            )}
            attributesRenderer={cell => ({ 'data-code': cell.code } || {})}
          />
        </Col>
        <Modal title="Lựa chọn kế hoạch" visible={this.state.visibleSelectPlan} footer={null}>
          <FormSelectPlan
            handleSelectPlan={this.handleSelectPlan}
            onCancel={this.handleCancelSelectPlan}
          />
        </Modal>
      </Row>
    );
  }
}



