import React, { Component } from 'react';
import { Form, Select, DatePicker } from 'antd';

const { Option } = Select;
const { RangePicker } = DatePicker;
@Form.create({ name: 'FormCreateNew' })
export default class FormCreateNew extends Component {
  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Form onSubmit={this.handleSubmit} className="login-form">
        {' '}
        <Form.Item label="Năm học" hasFeedback>
          {getFieldDecorator('codeRating', {
            initialValue: '',
            rules: [
              {
                required: true,
                message: 'Vui lòng nhập thông tin!',
              },
            ],
          })(
            <Select>
              <Option value="1">2015-2016</Option>
              <Option value="2">2016-2017</Option>
              <Option value="3">2017-2018</Option>
            </Select>
          )}
        </Form.Item>
        <Form.Item label="Ngày ">
          {getFieldDecorator('nnhh', {
            initialValue: '',
            rules: [
              {
                required: true,
                message: 'Vui lòng nhập thông tin!',
              },
            ],
          })(<RangePicker style={{ width: '100%' }} />)}
        </Form.Item>
      </Form>
    );
  }
}



