import React, { useState, useEffect } from 'react';
import Page from 'components/LayoutComponents/Page';
import { BtnAddNew } from 'components/common/button';
import { Card, Modal } from 'antd';
import moment from 'moment';
import { CartHeadAction } from 'components/common';
import FormSearch from './FormSearch';
import DataSheet from './DataSheet';
import FormCreateNew from './FormCreateNew';
import NotePlan from './NotePlan';
import YearEduPlanInfo from './YearEduPlanInfo';

const getWeekRange = (startDate, endDate) => {
  const result = [];
  let currentNoWeek = 0;
  let startDateCheck = startDate.clone();
  let i = 0;
  if (startDateCheck.day() === 0) {
    // if = Sunday
    startDateCheck = startDateCheck.add(1, 'day');
  }
  while (startDateCheck.isBefore(endDate) && i < 100) {
    const dayOfWeek = startDateCheck.day();
    const endWeekCurrentCheck = startDateCheck.clone().add(7 - dayOfWeek, 'day');
    result.push({
      noWeek: currentNoWeek + 1,
      month: startDateCheck.clone().format('MM/YYYY'),
      fromDate: startDateCheck.clone().format('DD/MM/YYYY'),
      toDate: endWeekCurrentCheck.clone().format('DD/MM/YYYY'),
    });
    currentNoWeek += 1;
    startDateCheck = endWeekCurrentCheck.clone().add(1, 'day');
    i += 1;
  }
  return result;
};

const WholeSchoolTrainingPlanPage = () => {
  const [visible, setVisible] = useState(false);
  const [yearEduPlanSelected, setYearEduPlanSelected] = useState(null);
  const [classIds, setClassIds] = useState([]);
  const [weekEdus, setWeekEdusEdus] = useState([]);

  useEffect(() => {
    if (!yearEduPlanSelected) {
      return;
    }
    const fromDate = moment(yearEduPlanSelected.fromDate);
    const toDate = moment(yearEduPlanSelected.toDate);
    setWeekEdusEdus(getWeekRange(fromDate.clone(), toDate.clone()));
  }, [yearEduPlanSelected]);

  const handleSubmitFormSearch = async values => {
    setYearEduPlanSelected(values.objSelected.yearEduPlanId);
    setClassIds([values.classId]);
  };

  return (
    <Card title="Kế hoạch đào tạo toàn trường">
      <CartHeadAction>
        <BtnAddNew onClick={() => setVisible(true)} />
      </CartHeadAction>
      <FormSearch handleSubmit={handleSubmitFormSearch} />
      <YearEduPlanInfo yearEduPlan={yearEduPlanSelected} />
      <DataSheet yearEduPlan={yearEduPlanSelected} classIds={classIds} weekEdus={weekEdus} />
      <NotePlan />
      <Modal title="Tạo kế hoạch mới" visible={visible}>
        <FormCreateNew />
      </Modal>
    </Card>
  );
};

export default props => (
  <Page {...props}>
    <WholeSchoolTrainingPlanPage />
  </Page>
);



