import React, { Component } from 'react';
import { Row, Col, Tag } from 'antd';
import { planType } from 'dump/data';

export default class NotePlan extends Component {
  state = {};
  render() {
    return (
      <Row style={{ marginTop: 20 }}>
        {Object.values(planType).map(plan => (
          <Col span={8}>
            <Tag color={plan.color}>{plan.code}</Tag>
            <span>{plan.name}</span>
          </Col>
        ))}
      </Row>
    );
  }
}



